.Movie {
    width: 1240px;
    margin: 80px auto 100px;
}

.Movie .TitleBox {
    margin-bottom: 24px;
}
.Movie h1.Title {
    font-size: 36px;
    font-weight: bold;
    color: #E0C097;
}

.Movie i.Explain {
    color: #aaa;
    font-size: 11px;

}

.MovieGallery {
    display: flex;
}

.MovieGallery .view {
    width: 55%;
    display: flex;
}

.MovieGallery .view img {
    width: 300px;
    height: 450px;
}

.MovieGallery .view div {
    width: 350px;
    margin-left: 20px;
    
}

.MovieGallery .view div h3 {
    font-size: 24px;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.MovieGallery .view div i {
    font-size: 11px;
    color: #aaa;
}

.MovieGallery .view div ul li {
    line-height: 40px;
    font-size: 14px;
    width: 100%;
    clear: both;
}

.MovieGallery .view div ul li em {
    float: left;
    width: 90px;
}
.MovieGallery .view div ul li:last-child em {
    height: 50px;
}
.MovieGallery .view div ul li p {
    display: inline;
}

.MovieGallery .list {
    width: 45%;
}

.MovieGallery .list table {
    width: 100%;
}

.MovieGallery .list table .w1 {
    width: 60px;
}

.MovieGallery .list table .w2 {}

.MovieGallery .list table .w3 {
    width: 120px;
}

.MovieGallery .list table .w4 {
    width: 120px;
}

.MovieGallery .list table .w5 {
    width: 60px;
}

.MovieGallery .list table th,
.MovieGallery .list table td {
   
    vertical-align: middle;
    border-bottom: 1px solid #ddd;
    text-align: center;
}

.MovieGallery .list table thead {}

.MovieGallery .list table tr {
    height: 38px;
}

.MovieGallery .list table tr th {
    height: 30px;
    border-bottom: 1px solid #aaa;
}

.MovieGallery .list tbody {}

.MovieGallery .list tbody tr {}

.MovieGallery .list tbody tr td:nth-child(2) { text-align-last: left; }
.modal .bg {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0; top: 0;
    background-color: rgba(0,0,0,0.7);
}

.modal .popup {
    width: 800px;
    height: auto;
    position: absolute;
    left: 50%; top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #eee;
    color: #222;
    padding: 30px 50px;
}

.modal .popup h2 {
    font-size: 18px;
    margin-bottom: 15px;
    font-weight: bold;
}

.modal .popup div {
    width: 400px;
    display: block;
    margin: 0 auto;
}

.modal .popup div img {
    display: block;
    width: 100%;
}

.modal .popup h3 {
    font-size: 14px;
    margin-top: 10px;
}

.modal .popup em {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 10; 
    -webkit-box-orient: vertical;
}

.modal .popup p.closeBtn {
    position: absolute;
    top: 10px; right: 10px;
    font-size: 20px;
    cursor: pointer;
}
